.program-selection__list {
  @include clear-list;

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }
}

.program-selection__item {
  font-family: "Arial", sans-serif;
  color: #444444;
  background-color: #f2f2f2;
  padding-top: 30px;
  padding-bottom: 18px;
  padding-left: 19px;

  &:not(:last-child) {
    margin-bottom: 20px;

    @media (min-width: $desktop-width) {
      margin-bottom: 0;
    }
  }

  @media (min-width: $tablet-width) {
    padding-left: 53px;
    padding-top: 75px;
    padding-bottom: 53px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 52px;
    padding-top: 77px;
    padding-bottom: 55px;
  }
}

.program-selection__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 154%;
  color: #000000;
  text-transform: uppercase;
  margin-top: 0;
  padding-left: 72px;
  margin-bottom: 29px;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 100%;
    padding-left: 0;
    margin-bottom: 25px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 162px;
    margin-bottom: 73px;
  }

  &--weight-loss {
    &::before {
      position: absolute;
      content: "";
      background-image: url("../img/cat-slim.svg");
      background-repeat: no-repeat;
      background-size: 50px 53px;
      width: 50px;
      height: 53px;
      transform: translate(-69px, -13%);

      @media (min-width: $tablet-width) {
        background-size: 200px 211px;
        width: 200px;
        height: 211px;
        transform: translate(401px, 0);
      }

      @media (min-width: $desktop-width) {
        background-size: 100px 106px;
        width: 100px;
        height: 106px;
        transform: translate(-162px, -30%);
      }
    }
  }

  &--weight-gain {
    &::before {
      position: absolute;
      content: "";
      background-image: url("../img/cat-pro.svg");
      background-repeat: no-repeat;
      background-size: 67px 50px;
      width: 67px;
      height: 50px;
      transform: translate(-75px, -13%);

      @media (min-width: $tablet-width) {
        background-size: 268px 200px;
        width: 268px;
        height: 200px;
        transform: translate(359px, 0);
      }

      @media (min-width: $desktop-width) {
        background-size: 134px 100px;
        width: 134px;
        height: 100px;
        transform: translate(-181px, -30%);
      }
    }
  }
}

.program-selection__catalog {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 100%;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;

  &:active {
    opacity: 0.3;
  }

  &::after {
    position: absolute;
    content: "";
    background-image: url("../img/icon-arrow.svg");
    background-repeat: no-repeat;
    background-size: 32px 12px;
    width: 32px;
    height: 12px;
    transform: translate(15px, 25%);

    @media (min-width: $tablet-width) {
      transform: translate(25px, 25%);
    }

    @media (min-width: $tablet-width) {
      transform: translate(25px, 25%);
    }
  }
}

.program-selection__description {
  position: relative;
  margin-top: 0;
  padding-right: 33px;
  padding-bottom: 23px;
  margin-bottom: 15px;

  @media (min-width: $tablet-width) {
    padding-right: 357px;
    line-height: 150%;
    margin-bottom: 25px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 51px;
    margin-bottom: 23px;
    padding-bottom: 0;
  }

  &--padding {
    @media (min-width: $desktop-width) {
      padding-right: 85px;
    }
  }

  &::after {
    position: absolute;
    content: "";
    border: 1px solid #d9d9d9;
    bottom: 0;
    left: 0;
    right: 20px;
    width: 240px;

    @media (min-width: $tablet-width) {
      display: none;
    }
  }
}
